import { SuperAdminAuthGuard } from './../../core/guards/auth.guard';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { DataViewComponent } from 'src/app/shared/components/data-view/data-view.component';
import { DownloadErrorComponent } from 'src/app/shared/components/download-error/download-error.component';
import { GeneralTableViewComponent } from './general-table-view.component';
import { AdminLendingComponent } from '../admin/components/admin-lending/admin-lending.component';

const routes: Routes = [
    {
        path: '',
        component: GeneralTableViewComponent,
        children: [
          {path: '', redirectTo: 'admin', pathMatch: 'full'},
          {path: 'admin', component: AdminLendingComponent, canActivate: [SuperAdminAuthGuard]},
          { path: 'data-view', component: DataViewComponent, canActivate: [SuperAdminAuthGuard]},
          { path: 'download-error', component: DownloadErrorComponent, canActivate: [SuperAdminAuthGuard]}
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class GeneralTableViewRoutingModule { }




