import { Component, OnInit } from '@angular/core';
import { locale as english } from 'src/app/shared/i18n/en';
import { locale as hindi } from 'src/app/shared/i18n/hi';


import { LanTranslationLoaderService } from 'src/app/core/services/translation-loader.service';

@Component({
  selector: 'app-general-table-view',
  templateUrl: './general-table-view.component.html',
  styleUrls: ['./general-table-view.component.scss']
})
export class GeneralTableViewComponent implements OnInit {

  /**
     * Constructor
     *
     * @param {LanTranslationLoaderService} _lanTranslationLoaderService
     */
    constructor(
      private _lanTranslationLoaderService: LanTranslationLoaderService
  )
  {    
      this._lanTranslationLoaderService.loadTranslations(english, hindi);
  }

  ngOnInit() {
  }

}
